@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');

.navbar {
    background-color: #393939;
    width: 100vw;
    max-width: 100%;
    height: 130px;
    padding: calc(10px + 1vh);
    padding-left: calc(20px + 2vh);
    padding-right: calc(20px + 2vh);

    display: flex;

    align-items: center;
    justify-content: space-between;

    font-family: 'Lato', sans-serif;
    font-weight: bold italic;
    color: white;

    position: relative;
    box-shadow: 0px 0px 106px rgba(0, 0, 0, 0);
    z-index: 4;
}

.button {
    border: none;
    outline: none;
    font-family: 'Lato', sans-serif;
    font-size: xx-large;
    text-decoration: none;
    font-style: bold;
    color: white;
    background: transparent;
    padding: calc(10px + 1vh);
}

a:hover{
    color: gray;
}

.welcome{
    text-decoration: none;
    color: white;
    font-weight: bold;
}

.logout {
    margin-left: 30px;
    font-size: x-large;
    font-style: italic;
    color: #EC4A4A;
    background: transparent;
    border: none;
    font-weight: bold;

}